export  class Formarea extends React.Component {
    constructor(){
      super(); 
      this.state ={
        dialogVisible: false,
        phoneNumber:"",
        code:"",
        // URL: "https://jr.tuniu.com/opl/promotion/getGift",
        // URL2: "https://jr.tuniu.com/opl/promotion/sendMessage",  //

        URL: "http://jr.tuniu.com:8181/opl/promotion/getGift",
        URL2: "http://jr.tuniu.com:8181/opl/promotion/sendMessage",  //   

        timeout:"",

      }
    }
    //
    popupWin(){
        if(!this.checkPhone(this.state.phoneNumber)){
            MessageBox.msgbox({
                title: '',
                message: '',
                type:"error",
                showCancelButton: false
              }).then((action)=>{
                  console.log(action)
                  document.getElementById("phoneNumber").focus();
              });
              return;
        }
        //
        this.setState({ dialogVisible: true });
        this.sendCode(); //

    }
    //
    getCoupon2() {

        //this.setState({ dialogVisible: false })
        let code = this.state.code;
        if (code == "") {   
          MessageBox.alert('', '');
          return;
        }

  
        //return;
        let request = {
          smsCode: code, //   String
          phoneNum: this.state.phoneNumber, //
          type: 1, //  1   
          desc: "" //  
        };
  
  
        axios.post(this.state.URL, request).then(response => {
           
            if (response.data.success) {
              //console.log( this.data)
              // alert("");
                MessageBox.alert('', '').then(() => {
                    //if (platform == 1) {
                    location.href = "http://jr.tuniu.com/opl/couponList"; //pc
                    //} else {
                    //location.href = "http://8.m.tuniu.com/xdm/m/index/ticket"; //Msite
                    //}
                });
            }else{
                MessageBox.alert(response.data.msg, '')             
            }
          }).catch(error => {
                MessageBox.alert("..", '');
                //console.log(error);
          });
      }
    sendCode(){
        if(this.state.timeout!=""){
            return;
        }
        
        let req={
            phone:this.state.phoneNumber,  //    
            type:2 // 12
        }
       axios.post(this.state.URL2,req).then(response => {
           if (response.data.success) {
                Notification({
                    title: '',
                    message: '.'+ this.state.code,
                    type: 'success'
                });  
           }else{           
               this.setState({timeout:""}); 
                 Message({
                    type: 'error',
                    message: ''
                });
           }
        });

        let i=0, second=60; //
        let to=setInterval(_=>{
            if(i<second){              
                this.setState({timeout:(second-i)});
                i++;
            }else{
                clearInterval(to);
                this.setState({timeout:""});                
                i=0;                   
            }	
        },1000);

    

    }
    checkPhone(phone) {
        if (!/^1[345789]\d{9}$/.test(phone)) {
          return false;
        }
        return true;
    }
    render(){
        return(
           
            <div className="formarea">
                    <div><img src={btn_area} /></div>
                    <div className="operate">
                        <input type="text" value={this.state.phoneNumber} className="phoneNumber" id="phoneNumber" placeholder="" maxLength="11" onChange={(event)=>{ this.setState({phoneNumber: event.target.value})}} />
                        <img onClick={ () => this.popupWin() } src={btn} />
                    </div>
                       <Dialog
                        title=""
                        size="tiny"
                        top="30%"
                        className="codeArea" 
                        visible={ this.state.dialogVisible }
                        onCancel={ () => this.setState({ dialogVisible: false }) }
                        lockScroll={ true }
                        closeOnClickModal={false}
                        >
                            <Dialog.Body>
                                
                                        <input type="text" value={this.state.code}  placeholder="" onChange={(event)=>{ this.setState({code:event.target.value})}} className="codeInput" maxLength="6"/>
                                                                        
                                        <Button onClick={ () => this.sendCode()}>{this.state.timeout}</Button>
                                        
                                
                            </Dialog.Body>
                            <Dialog.Footer className="dialog-footer" style={{textAlign: "right"}}>
                                {/* <Button onClick={ () => this.setState({ dialogVisible: false }) }></Button> */}
                                <Button type="primary" onClick={ () => this.getCoupon2() }></Button>
                            </Dialog.Footer>
                        </Dialog>
          </div>      
           

            
            
        )
    }
}



